import * as React from 'react';
import {DataGrid, GridLogicOperator, GridToolbar, GridToolbarQuickFilter} from '@mui/x-data-grid';
import Button from "@mui/material/Button";
import {useEffect, useState} from "react";
import Box from "@mui/material/Box";
import axios from "axios";
import AddBookDialog from "../modals/AddBookDialog";
import AddDvdDialog from "../modals/AddDvdDialog";
import AddAudioBookDialog from "../modals/AddAudioBookDialog";
import Grid from "@mui/material/Grid";
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import {TabPanel, a11yProps} from "../components/TabPanel.jsx";
import AddReferenceBookDialog from "../modals/AddReferenceBookDialog.jsx";
import EditCategoriesDialog from "../modals/edit_dialogs/EditCategoriesDialog.jsx";
import IconButton from '@mui/material/IconButton';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';

const SERVER_OPTIONS = {
    useCursorPagination: false,
};

// const {useQuery, ...data} = createFakeServer({}, SERVER_OPTIONS);

function preventDefault(event) {
    event.preventDefault();
}

const columns = [
    {field: 'id', headerName: 'ID', width: 75},
    {
        field: 'title',
        headerName: 'Title',
        width: 180,
        editable:true,
    },
    {
        field: 'author',
        headerName: 'Author',
        width: 120,
        editable:true,
    },
    {
        field: 'category',
        headerName: 'Category',
        width: 100,
        editable:true,
    },
    {
        field: 'type',
        headerName: 'Type',
        width: 110,
        editable:true,
    },
    {
        field: 'pages',
        headerName: 'Pages',
        width: 80,
        editable:true,
    },
    {
        field: 'runTimeMinutes',
        headerName: 'RunTimeMinutes',
        width: 150,
        editable:true,
    },
    {
        field: 'isBorrowable',
        headerName: 'IsBorrowable',
        type: "boolean",
        width: 100,
        editable:true,
    },
    {
        field: 'borrower',
        headerName: 'Borrower',
        width: 120,
        editable:true,
    },
    {
        field: 'borrowDate',
        headerName: 'BorrowDate',
        width: 100,
        editable:true,
    },
    {
        field: 'actions',
        headerName: 'Actions',
        width: 140,
        renderCell: (params) => (
            <>
                <IconButton aria-label="delete" onClick={() => handleDelete(params.row)}>
                    <DeleteIcon fontSize="inherit" />
                </IconButton>
            </>
        ),
    },

    // {
    //     field: 'isManager',
    //     headerName: 'IsManager',
    //     description: 'This column has a value getter and is not sortable.',
    //     sortable: true,
    //     editable: false,
    //     width: 160,
    //     valueGetter: (params) =>
    //         `${params.row.firstName || ''} ${params.row.lastName || ''}`,
    // },

];

function handleDelete(row) {
    console.log('Deleting row:', row);
}

export default function LibItemTable() {
    const [useOnce, setUse] = useState(true)
    const [rows, setRows] = useState([])
    const [isLoading, setLoading] = useState(true)
    const [totalRowCount, setTotalRowCount] = useState(0)

    const [isOpenAddBook, setOpenAddBook] = React.useState(false);
    const [isOpenAddDvd, setOpenAddDvd] = React.useState(false);
    const [isOpenAddAudioBook, setOpenAddAudioBook] = React.useState(false);
    const [isOpenAddReferenceBook, setOpenAddReferenceBook] = React.useState(false);

    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const fetchData = ({page, pageSize}) => {
        setLoading(true)
        // axios.get("/api/books?limit=" + pageSize + "&page=" + page)
        axios.get("/api/books")
            .then(response => {
                return response.data
            })
            .then(data => {
                setTotalRowCount(data.size)
                setRows(data?.books || [])
                setLoading(false)
            })
            .catch((e) => console.log(e))
    }

    const addRow = (row)=>{
        setRows([...rows,row])
    }

    // const { isLoading, rows, pageInfo } = useQuery(paginationModel); //Fake data

    const [paginationModel, setPaginationModel] = React.useState({
        page: 0,
        pageSize: 5,
    });

    React.useEffect(() => {
        fetchData(paginationModel)
    }, [useOnce]);

    const handlerPaginationModel = ({pageSize, page}) => {
        //fetchData({page, pageSize})
        setPaginationModel({page, pageSize})
    }

    const [filterModel, setFilterModel] = React.useState({items: []});
    const [columnVisibilityModel, setColumnVisibilityModel] = React.useState({});

    let filterModels = [
        {items: [{id: 100,field: 'type', operator: 'equals', value: 'book'}]},
        {items: [{id: 100,field: 'type', operator: 'equals', value: 'dvd'}]},
        {items: [{id: 100,field: 'type', operator: 'equals', value: 'audio_book'}]},
        {items: [{id: 100,field: 'type', operator: 'equals', value: 'reference_book'}]},
    ]
    let columnVisibilityModels=[
        {type:false,runTimeMinutes:false},
        {type:false,author:false,pages:false},
        {type:false,author:false,pages:false},
        {type:false,runTimeMinutes:false},
    ]

    useEffect(()=>{
        setFilterModel(filterModels[value])
        setColumnVisibilityModel(columnVisibilityModels[value])
    },[value])

    const handlerChangeRow = (data,e) => {

        if(data.reason === "rowFocusOut") {
            e.defaultMuiPrevented = true
            if(e.preventDefault) e.preventDefault()
        }

        // apiRef.current.stopRowEditMode()
    }


    function handleEdit(row) {
        console.log('Editing row:', row);
    }

    function handleDelete(row) {
        console.log('Deleting row:', row);
    }

    const processRowUpdate = React.useCallback(
        async (newRow,oldRow) => {

            // axios.post(){
            //
            // }

            return oldRow;
        },
        [],
    );

    return (
        <React.Fragment>
            <Tabs centered value={value} onChange={handleChange}>
                <Tab label="Book"/>
                <Tab label="DVD"/>
                <Tab label="Audio Book"/>
                <Tab label="Reference Book"/>
            </Tabs>
            {/*<TabPanel value={value} index={0}>*/}
            <Box sx={{height: 600, width: '100%'}}>
                <DataGrid
                    rows={rows}
                    columns={columns}
                    rowCount={totalRowCount}

                    editMode="row"
                    paginationModel={paginationModel}
                    //paginationMode="server"
                    onPaginationModelChange={handlerPaginationModel}
                    pageSizeOptions={[5,10,20,30]}
                    loading={isLoading}
                    slots={{toolbar: GridToolbarQuickFilter,}}
                    filterModel={filterModel}
                    onFilterModelChange={(newFilterModel) => {setFilterModel(newFilterModel)}}
                    columnVisibilityModel={columnVisibilityModel}
                    onColumnVisibilityModelChange={(newModel) => setColumnVisibilityModel(newModel)}
                    onRowEditStop={handlerChangeRow}
                    processRowUpdate={processRowUpdate}

                    checkboxSelection
                    disableRowSelectionOnClick
                />
            </Box>
            {/*</TabPanel>*/}
            <Grid>
                <Button color="primary" href="#" onClick={() => {
                    setOpenAddBook(true)
                    setValue(0)
                }} sx={{mt: 3}}>
                    Add book
                </Button>
                <Button color="primary" href="#" onClick={() => {
                    setOpenAddDvd(true)
                    setValue(1)
                }} sx={{mt: 3}}>
                    Add DvD
                </Button>
                <Button color="primary" href="#" onClick={() => {
                    setOpenAddAudioBook(true)
                    setValue(2)
                }} sx={{mt: 3}}>
                    Add Audio Book
                </Button>
                <Button color="primary" href="#" onClick={() => {
                    setOpenAddReferenceBook(true)
                    setValue(3)
                }} sx={{mt: 3}}>
                    Add Reference Book
                </Button>
            </Grid>

            <AddBookDialog addRow={addRow} open={isOpenAddBook} handleClose={() => setOpenAddBook(false)}/>
            <AddDvdDialog open={isOpenAddDvd} handleClose={() => setOpenAddDvd(false)}/>
            <AddAudioBookDialog open={isOpenAddAudioBook} handleClose={() => setOpenAddAudioBook(false)}/>
            <AddReferenceBookDialog open={isOpenAddReferenceBook} handleClose={() => setOpenAddReferenceBook(false)}/>
            {/*<EditCategoriesDialog open={isOpenAddReferenceBook} handleClose={() => setOpenAddReferenceBook(false)}/>*/}
        </React.Fragment>
    );
}

