import * as React from 'react';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Dialog from '@mui/material/Dialog';
import DialogActions from '@mui/material/DialogActions';
import DialogContent from '@mui/material/DialogContent';
import DialogTitle from '@mui/material/DialogTitle';
import Grid from "@mui/material/Grid";
import TextField from "@mui/material/TextField";

export default function AddReferenceBookDialog({open, handleClose}) {
    const handleSubmit = (event) => {
        event.preventDefault();
        const data = new FormData(event.currentTarget);

        let object = {};
        data.forEach((value, key) => object[key] = value);
        console.log(JSON.stringify(object));

        handleClose()
    }

    return (
        <React.Fragment>
            <Dialog
                fullWidth={true}
                maxWidth={'sm'}
                open={open}
            >
                <form onSubmit={handleSubmit}>
                    <DialogTitle>New Reference Book</DialogTitle>
                    <DialogContent>
                        <Grid container spacing={3}>

                            <Grid item sm={6}>
                                <TextField
                                    required
                                    id="title"
                                    name="title"
                                    label="Title"
                                    fullWidth
                                    autoComplete="given-name"
                                    variant="standard"
                                />
                            </Grid>

                            <Grid item sm={6}>
                                <TextField
                                    required
                                    id="author"
                                    name="author"
                                    label="Author"
                                    fullWidth
                                    autoComplete="given-name"
                                    variant="standard"
                                />
                            </Grid>

                            <Grid item sm={6}>
                                <TextField
                                    required
                                    id="category"
                                    name="category"
                                    label="Category"
                                    fullWidth
                                    autoComplete="given-name"
                                    variant="standard"
                                />
                            </Grid>

                            <Grid item sm={6}>
                                <TextField
                                    required
                                    id="pages"
                                    name="pages"
                                    label="Pages"
                                    fullWidth
                                    autoComplete="given-name"
                                    variant="standard"
                                />
                            </Grid>

                            <Grid item xs={10}>
                                <p>Important - Reference books can not be borrowed</p>
                                {/*<FormControlLabel*/}
                                {/*    control={<Checkbox color="secondary" name="is-borrowable" value="no" />}*/}
                                {/*    label="IsBorrowable. "*/}
                                {/*    disabled*/}
                                {/*/>*/}
                            </Grid>

                        </Grid>
                        <Box
                            noValidate
                            component="form"
                            sx={{
                                display: 'flex',
                                flexDirection: 'column',
                                m: 'auto',
                                width: 'fit-content',
                            }}
                        >
                        </Box>
                    </DialogContent>
                    <DialogActions>
                        <Button type="submit" onClick={handleClose}>Submit</Button>
                        <Button autoFocus onClick={handleClose}>Cancel</Button>
                    </DialogActions>
                </form>
            </Dialog>
        </React.Fragment>
    );
}