import * as React from 'react';
import {DataGrid, GridToolbarQuickFilter} from '@mui/x-data-grid';
import Button from "@mui/material/Button";
import Box from "@mui/material/Box";
import axios from "axios";
import AddEmployeeDialog from "../modals/AddEmployeeDialog";
import {useEffect, useState} from "react";
import Tabs from "@mui/material/Tabs";
import Tab from "@mui/material/Tab";
import {a11yProps} from "../components/TabPanel.jsx";
import IconButton from "@mui/material/IconButton";
import DeleteIcon from "@mui/icons-material/Delete.js";
// import EditEmployeeDialog   from "../modals/edit_dialogs/EditEmployeeDialog";


const SERVER_OPTIONS = {
    useCursorPagination: false,
};

// const {useQuery, ...data} = createFakeServer({}, SERVER_OPTIONS);

const columns = [
    {field: 'id', headerName: 'ID', width: 90},
    {
        field: 'firstName',
        headerName: 'First name',
        width: 150,
    },
    {
        field: 'lastName',
        headerName: 'Last name',
        width: 150,
    },
    {
        field: 'salary',
        headerName: 'Salary',
        type: 'number',
        width: 110,
    },
    {
        field: 'managerId',
        headerName: 'ManagerId',
        width: 150,
    },
    {
        field: 'isCeo',
        headerName: 'IsCEO',
        description: 'Only 1 CEO at a time.',
        type: 'boolean',
        width: 160,
        sortable: true,
    },
    {
        field: 'isManager',
        headerName: 'IsManager',
        description: 'Feudal lord',
        type: 'boolean',
        width: 160,
        sortable: true,
    },
    {
        field: 'actions',
        headerName: 'Actions',
        width: 140,
        renderCell: (params) => (
            <>
                <IconButton aria-label="delete" onClick={() => handleDelete(params.row)}>
                    <DeleteIcon fontSize="inherit" />
                </IconButton>
            </>
        ),
    },

];

function handleDelete(row) {
    console.log('Deleting row:', row);
}

export default function EmployeePage() {
    const [useOnce, setUse] = useState(true)
    const [rows, setRows] = useState([])
    const [rowsFilter, setRowsFilter] = useState([])
    const [isLoading, setLoading] = useState(true)
    const [totalRowCount, setTotalRowCount] = useState(0)

    const [isOpenAddEmployee, setOpenAddEmployee] = useState(false);

    const [value, setValue] = React.useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    const fetchData = ({page, pageSize}) => {
        setLoading(true)
        // axios.get("/api/employees?limit=" + pageSize + "&page=" + page)
            axios.get("/api/employees")
            .then(response => {
                return response.data
            })
            .then(data => {
                setTotalRowCount(data.size)
                setRows(data?.employees || [])
                setLoading(false)
            })
            .catch((e) => console.log(e))
    }

    // const { isLoading, rows, pageInfo } = useQuery(paginationModel); //Fake data

    const [paginationModel, setPaginationModel] = React.useState({
        page: 0,
        pageSize: 5,
    });

    React.useEffect(() => {
        fetchData(paginationModel)
    }, [useOnce]);

    const handlerPaginationModel = ({pageSize, page}) => {
        // fetchData({page, pageSize})
        setPaginationModel({page, pageSize})
    }

    const [filterModel, setFilterModel] = React.useState({items: []});
    // const [columnVisibilityModel, setColumnVisibilityModel] = React.useState({});

    // Because I do not want to pay for Premium MUI, I had to blow my head to come with a solution to having
    // multiple filtering arguments for the tabs. Alternative to filterModels.
    let filters = [
        (row)=> !row.isManager && !row.isCeo,
        (row)=>row.isManager,
        (row)=>row.isCeo,
    ]

    useEffect(()=> {
        let filtered = rows.filter(filters[value]); //Not nice, but I had no choice/time.
        setRowsFilter(filtered)
        setTotalRowCount(filtered.length)
    },[rows,value])


    return (
        <React.Fragment>
            <Tabs centered value={value} onChange={handleChange}>
                <Tab label="Mortal Employees" {...a11yProps(0)}/>
                <Tab label="Managers" {...a11yProps(1)}/>
                <Tab label="CEO the Imperor" {...a11yProps(2)}/>
            </Tabs>
            {/*<TabPanel value={value} index={0}>*/}
            <Box sx={{height: 600, width: '100%'}}>
                <DataGrid
                    rows={rowsFilter}
                    columns={columns}
                    rowCount={totalRowCount}

                    paginationModel={paginationModel}
                    //paginationMode="server"
                    onPaginationModelChange={handlerPaginationModel}
                    pageSizeOptions={[5, 10]}
                    loading={isLoading}
                    slots={{toolbar: GridToolbarQuickFilter,}}
                    filterModel={filterModel}
                    disableMultipleColumnsFiltering={false}
                    onFilterModelChange={(newFilterModel) => {setFilterModel(newFilterModel)}}
                    // columnVisibilityModel={columnVisibilityModel}
                    // onColumnVisibilityModelChange={(newModel) => setColumnVisibilityModel(newModel)}
                    // onChangePage={fetchData}

                    checkboxSelection
                    disableRowSelectionOnClick
                />
            </Box>
            <Button color="primary" href="#" onClick={() => setOpenAddEmployee(true)} sx={{mt: 3}}>
                Add employee
            </Button>

            <AddEmployeeDialog open={isOpenAddEmployee} handleClose={() => setOpenAddEmployee(false)}/>
        </React.Fragment>
    );
}
